body {
    font: 18px / 26px 'Circe';
    font-weight: 300;
    width: 100%;
    height: 100%;
    overflow: auto;
    color: #2B317C;
    background: #282a52 url("../images/background.png") top left;
}

* {
    margin: 0;
    padding: 0;
}

a {
    color: #282a52;
    outline: none;
    transition-property: background, border-color, color, opacity;
    transition-duration: .2s;
    transition-timing-function: ease-in;
}

@include media-breakpoint-down(sm) {

    .col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .col, .col-auto, .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm, .col-sm-auto, .col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12, .col-md, .col-md-auto, .col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg, .col-lg-auto, .col-xl-1, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl, .col-xl-auto {
        padding: 0 10px;
    }

    .row {
        margin: 0 -10px;
    }
    .container {
        padding: 0 10px;
    }


}

.content {
    font-size: 18px;
    color: #2B317C;
    line-height: 21px;
    position: relative;

    &--white {
        color: #ffffff;
    }
}

.content--upped {
    margin-top: -20px;
}

.content--transparent {
    background: transparent;
    padding: 0;
}

.content--border {
    border: 3px solid #3932B4;
    padding: 54px 111px;
    font-size: 18px;
    color: #271f5e;
    margin-bottom: 50px;
    line-height: 21px;
    position: relative;
    border-radius: .2em;
    @include media-breakpoint-down(md) {
        padding: 20px 55px;
        margin-bottom: 20px;
    }

    * {
        z-index: 2;
        position: relative;
    }

    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 2px;
        left: 2px;
        bottom: 1px;
        z-index: 0;
        border: 1px solid #3932B4;
        width: calc(100% - 4px);
        height: calc(100% - 4px);
    }
}

.content--blue {
    background-image: linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    border: 3px solid white;
    margin-bottom: 30px;
    padding: 20px;
    font-size: 18px;
    color: #271f5e;
    margin-bottom: 14px;
    line-height: 21px;
    margin: 0 0 20px 0;
    position: relative;
    * {
        z-index: 2;
        position: relative;
    }
    @include media-breakpoint-up(xl) {
        padding: 30px;
    }
    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 5px;
        left: 5px;
        bottom: 5px;
        z-index: 0;
        border: 2px solid white;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
    }
}

.content__title {
    display: block;
    text-align: center;
    position: absolute;
    z-index: 0;
    top: -15px;
    left: 0;
    text-align: center;
    width: 100%;

    h4 {
        display: inline-block;
        margin: 0 !important;
        background: white;
        padding: 0 15px;
        @include media-breakpoint-down(sm) {
            margin: 0 20px !important;
            display: block;
        }
    }

}

