.subcats {
    margin-bottom: 33px;
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        @include media-breakpoint-up(xl) {
            padding: 0 15px;
        }
    }

    li {
        text-align: center;
        padding: 0 2px;
        &:before {
            display: none !important;
        }
        margin-bottom: 15px !important;
        a {
            font-size: 16px;
            padding: 5px 10px;

            display: inline-block;
            text-decoration: none;
            text-align: center;
            font-weight: 600;
            color: rgba(255, 255, 255, .6);
