.text--black {
    color: #000 !important;
    a {
        color: #000 !important;
    }
}

hr {
    height: 2px;
    background: rgb(0, 138, 209);
    display: block;
    width: 100%;
    margin: 0px 0 20px 0;
    border: none;
}

.form-select {
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    //background-image: url(data:image/svg+xml;base64,);
    //background-size: 18px 7px;
    //background-repeat: no-repeat;
    //background-position: right 30px center;
	background: linear-gradient(92deg, #3932B4 1.68%, #5A36BD 51.68%);
    border: none;
    color: #ffffff;
    display: block;
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 40px;
    padding: 16px 30px 14px 30px;
    cursor: pointer;
    height: 50px;
    border-radius: 0;

    option {
        color: #3932B4;
    }
}

.pagination {
    ul {
        margin: 0;
        padding: 0;
        padding-top: 20px;
        list-style: none;
        text-align: center;
        display: block;
    }
    li {
        display: inline-block;
        
        &:before {
            display: none !important;
        }

        a {
            border: 2px solid #2B317C;
            text-decoration: none;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            height: 50px;
            width: 50px;
            color: #2B317C;
            font-size: 24px;
            margin: 0 8px;
            
            @include media-breakpoint-down(lg) {
                height: 40px;
                width: 40px;
                font-size: 21px;
                margin: 0 5px;
                padding-top: 4px;
            }
            
            @include media-breakpoint-down(sm) {
                 height: 30px;
                width: 30px;
                font-size: 16px;
                margin: 0 3px;
            }            
            
            &:hover {
                color: white;
                background: #2B317C;
            }
        }

        &.active {
            a {
                color: white;
                background: #2B317C;
            }
        }

        &.control {
            a {
                border: none;
                width: 50px;
                background-image: url(data:image/svg+xml;base64,);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                @include media-breakpoint-down(sm) {
                    width: 35px;
                }
                &:hover {
                    background-color: transparent;
                    opacity: .5;
                }
            }
            &:last-child {
                a {
                    margin-left: 40px;
                    transform: rotate(180deg);
                    @include media-breakpoint-down(sm) {
                        margin-left: 15px;
                    }
                    @include media-breakpoint-down(lg) {
                         margin-left: 25px;
                    }
                }
            }
            &:first-child {
                a {
                    margin-right: 40px;
                    @include media-breakpoint-down(sm) {
                        margin-right: 15px;
                    }
                    @include media-breakpoint-down(lg) {
                         margin-right: 25px;
                    }
                }
            }
        }

    }
}

.silver-list {
    background: linear-gradient(119.32deg, #3932B4 1.65%, #5A36BD 98.35%);
    padding: 20px;
    margin: 0 0 10px 0 !important;
    list-style: none;

    a {
        color: #ffffff;
        text-decoration: none;
        font-size: 18px;
        font-weight: 300
    }
}

.silver-list__button {
    a {
        padding: 25px 20px;
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 10px;
        color: #ffffff;
        font-size: 18px;
        font-weight: 300;
        background: linear-gradient(119.32deg, #3932B4 1.65%, #5A36BD 98.35%);
        border-radius: .2em;
        svg {
            transition: all .35s ease;
            flex-shrink: 0;
            margin-left: 5px;
        }
        &.open {
            svg {
                transform: rotate(-180deg);
            }
        }
        @include media-breakpoint-up(md) {
            padding: 25px 40px;
        }
        @include media-breakpoint-up(lg) {
            padding: 32px 45px 30px 45px;
            font-size: 24px;
        }
        @include media-breakpoint-up(xl) {
            padding: 32px 73px 30px 109px;
        }

    }
}

.heading--big {
    margin: 30px 0;

    @include media-breakpoint-down(md) {
        margin: 20px 0 40px !important;
    }

    a {
        display: block;
        text-decoration: none;
        text-align: center;
        font-weight: 600;
        color: #ffffff;
        white-space: nowrap;
        border-radius: 50px;
        font-size: 20px;
        line-height: 1.25;
        padding: 9px 35px 5px;
        height: 42px;
        background: linear-gradient(106.06deg, #3932B4 1.68%, #5A36BD 51.68%);
        @include media-breakpoint-down(md) {
            font-size: 18px;
            line-height: 1.45;
            padding: 8px 15px 5px;
        }

        @include media-breakpoint-down(sm) {
            display: none;
        }
        .icon-arrow-left {
            @include media-breakpoint-down(md) {
                display: none;
            }
        }
    }

    h1, h2, h3, h4, h5, h6 {
        color: #ffffff;
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 800;
        padding: 0;
        font-family: "Gotham Pro";
