    }

    &:before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        background: -moz- oldlinear-gradient(358.55deg,#3932b4 1.68%,#5a36bd 51.68%);
        background: linear-gradient(91.45deg,#3932b4 1.68%,#5a36bd 51.68%);
        border-radius: .2em;
    }
}


