.icon-arrow-right{
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 60px;
    height: 21px;
    background: transparent url(data:image/svg+xml;base64,) no-repeat center / contain;
    margin-left: 10px;
    &:before,
    &:after {
        content: '';
        position: absolute;
        right: 10px;
        top: 50%;
        width: 10px;
        height: 2px;
        background-color: #ffffff;
        display: inline-block;

    }
    &:before{
        transform: rotate(42deg);
        top: calc(50% - 4px);
    }
    &:after{
        transform: rotate(-42deg);
        top: calc(50% + 3px);
    }

    &--long {
        width: 140px;
        background-image: url(data:image/svg+xml;base64,);

    }
}
