.contacts__icons {
    padding-top: 25px;
    padding-bottom: 25px;
}

.contacts__icons-item {
    margin-bottom: 25px;

    img {
        display: inline-block;
        vertical-align: middle;
        margin-right: 40px;
        width: 50px;
        height: 50px;
        @include media-breakpoint-down(sm) {
            display: block;
            margin-bottom: 20px;
        }
    }

    >div {
        vertical-align: middle;
        display: inline-block;
        font-size: 18px;
        line-height: 1.333;

        @include media-breakpoint-up(md) {
            font-size: 24px;
        }
    }
}

.contacts__form {
    background: linear-gradient(93.35deg, #3932B4 1.65%, #5A36BD 98.35%);
    padding: 55px 85px;
    position: relative;
    border-radius: .25em;
    @include media-breakpoint-down(sm) {
        padding: 55px 35px;
        margin: 30px -10px;
    }
    
    form {
        position: relative;
        z-index: 2;
    }

    @include media-breakpoint-up(xl) {
        height: 100%;
    }

    @include media-breakpoint-down(md) {
        margin-top: 80px;
    }

    &:before {
        content: '';
        display: block;
        position: absolute;
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        border: 2px solid white;
        left: 10px;
        top: 10px;
        z-index: 1;
        border-radius: .25em;
    }
}

.contacts__form-title {
    font-size: 24px;
    text-transform: none;
    font-weight: 400;
    text-align: center;
    color: white;
    margin-bottom: 10px;
}