.video-slider__item {
    display: block;
    height: 565px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top;
    cursor: pointer;
    margin-top: 15px;
    border-radius: .8em;
    overflow: hidden;
    @include media-breakpoint-down(lg) {
        height: 350px;
    }

    &:after {
        display: block;
        content: '';
        height: 162px;
        width: 162px;
        background: center no-repeat;
        background-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNTQiIGhlaWdodD0iNjQiIHZpZXdCb3g9IjAgMCA1NCA2NCIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48cGF0aCBkPSJNNTQgMzJMMCA2My4xNzY5TDAgMC44MjMwODZMNTQgMzJaIiBmaWxsPSJ3aGl0ZSIvPjwvc3ZnPg==), linear-gradient(92deg, #3932B4 1.65%, #5A36BD 98.35%);
        background-position: 57% 52%;
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        margin: 0 auto;
        border-radius: 50%;
        transform: translate(0, -50%);
        transition: all ease .35s ;
    }

    &:before {
        display: block;
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: transparent;
        transition: all ease .35s ;
        background: linear-gradient(94.83deg, #3932B4 1.65%, #5A36BD 98.35%);
        opacity: 0.65;
    }

    &:hover {
        &:before {
            opacity: .4;
        }
        &:after {
            transform: translate(0, -50%) scale(1.05);
        }
    }

}