.irs {
    margin-bottom: 20px;
    height: 60px;
}

.irs-slider {
   border: 4px solid #5A36BD;
    border-radius: 50%;
    background: #ffffff;
    height: 28px;
    width: 28px;
    top: -9px;
    cursor: pointer;
    transition: .3s ease border-width;
}

.irs-slider:hover {
    border-width: 3px;
}

.irs-line {
    height: 10px;
    background: #EEEEEE;
    border-radius: 50px;
}

.irs-bar-edge {
    height: 10px;
    background: #3932B4;
    top: 0;
    width: 20px;
    border-radius: 50px 0 0 50px;
}

.irs-bar {
    transition: unset;
    height: 10px;
    background: #3932B4;
    top: 0;
    border-radius: 50px;
}

.irs-single {
    display: none;
}

.irs-min, .irs-max {
    background: none;
    margin-top: 15px;
    visibility: visible !important;
}

.slider__title {
    margin-bottom: 30px;

    .text {
        font-size: 24px;
        @include media-breakpoint-down(sm) {
            font-size: 20px;
        }
    }

    .ress {
        font-size: 30px;
        font-weight: 400;
        @include media-breakpoint-down(sm) {
            font-size: 24px;
        }
    }
}

.calculator {
    .form-select {
        margin-bottom: 20px;
        padding-right: 70px;
    }

    h3 {
        font-size: 24px;
    }

    .content--border {
        margin-bottom: 30px;
    }
}

.calculator__price {
    font-size: 48px;
    line-height: 48px;
    font-weight: 400;
    color: #ffffff;
    text-transform: none;
}

.calculator__sales {
    margin: 15px 0;
}

.calculator__sales-item {
    background: transparent left center no-repeat;

    margin: 0 0 25px 0;
    padding: 5px 0 0 20px;

    span {
        font-size: 24px;
        line-height: 30px;
    }

    //&:nth-child(1) {
    //    background-image: url(data:image/svg+xml;base64,);
    //}
	//
    //&:nth-child(2) {
    //    background-image: url(data:image/svg+xml;base64,);
    //}

}

.calculator__result-header {
    padding: 20px 40px 35px 40px;

    > h5 {
        font-size: 24px;
    }

    ul {
        margin: 22px 0 20px 0;
        li {
            margin-bottom: 0;
        }
    }

    p {
        margin: 15px 0 0 0;
    }

}

.calculator__result-footer {
    background: #232a49;
    margin: 18px 0 0;
    padding: 20px;
    position: relative;

    &:after {
        background: url("../images/str_dropdown.png") no-repeat scroll 0 0 transparent;
        content: "";
        position: absolute;
        top: -11px;
        left: 50%;
        margin-left: -21px;
        width: 43px;
        height: 20px;
    }

    h5 span {
        font-size: 24px;
        font-weight: bold;
        text-transform: uppercase;
    }

    .item {
        margin-bottom: 20px;
    }

}

// old

.calculator .line_item {
    width: 100%;
    display: flex;

    p {
        margin: 0;
    }

    .right {
        text-align: right;
    }

    .center {
        flex-grow: 1;
        border-bottom: 1px dotted #9498A4;
        margin: 0 10px 6px 10px;
    }

    .left {
        padding: 0;
    }

}

.calculator form .left, .calculator form .left {
    padding: 20px 0;
    position: relative;
}

.line_slider > div > p {
    font-size: 18px;
    margin-left: 0;
    margin-top: 8px;
}

.sel_radio {
    margin-bottom: 20px;

    .label {
        display: block;
        font-size: 24px;
        margin-bottom: 10px;
    }
}

.calculator form > .left {
    background-color: #fff;
    padding-bottom: 0;
    position: relative;
    width: 640px;
}

.jq-checkbox {
    background: transparent url("../images/check_no.png") no-repeat scroll 0 0;
    height: 33px;
    margin: -7px 0 0 10px;
    vertical-align: middle;
    width: 76px;
    cursor: pointer;
}

.jq-checkbox.checked {
    background: url(../images/check_yes.png) no-repeat scroll 0 0 transparent;
}

.jq-radio.checked + label {
    background: linear-gradient(91.34deg, #3932B4 1.65%, #5A36BD 98.35%);
    color: white;
    border-color: transparent;
}

.jq-radio + label {
    font-family: "Gotham Pro";
    border: 2px solid #ffffff;
    border-radius: .25em;
    padding: 10px 14px;
    text-decoration: none;
    color: #ffffff;
    font-size: 18px;
    font-weight: 300;

    @include media-breakpoint-up(lg) {
        font-size: 24px;
        padding: 12px 30px 11px 30px;
    }
}

.jq-radio + label:hover {
    cursor: pointer;
}

.calculator form > .left:before {
    background: transparent url("../images/calc_before.png") no-repeat scroll 0 0;
    content: "";
    height: 51px;
    position: absolute;
    right: -22px;
    top: 71px;
    width: 25px;
    z-index: -1;
}

.calculator__result-footer-text {
    margin: 45px 0 25px 0;
}