.wrap-popup {
    display: none;
    padding: 6px;
    background: linear-gradient(129.9deg, #3932B4 1.65%, #5A36BD 98.35%);
    text-align: center;
    max-width: 680px;
    padding: 3px;
    border-radius: .5em;

    @include media-breakpoint-down(sm) {
        max-width: initial;
    }

    .container {
        padding: 5px !important;
    }

}

.wrap-popup form {
    border: 2px solid #fff;
    padding: 50px 80px;
    border-radius: .3em;

    @include media-breakpoint-down(sm) {
        padding: 30px 20px;
    }
}

.wrap-popup__title {
    font-family: "Gotham Pro";
    font-size: 36px;
    color: #fff;
    line-height: 26px;
    text-transform: uppercase;
    white-space: nowrap;
    margin: 0 0 40px 0;

    @include media-breakpoint-down(sm) {
        font-size: 20px;
    }

    a {
        color: #fff;
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }
}

.wrap-popup__text {
    font-size: 22px;
    color: #fff;
    line-height: 26px;
    margin: 20px 0;

    a {
        color: #fff;
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }
}

.fancybox-close-small {
    height: 50px;
    width: 50px;

    svg {
        color: white;
        opacity: 1;
    }

    &:hover {
        svg {
            opacity: .5;
        }
    }
}