}

.btn-figured {
    color: #000;
    font-size: 20px;
    line-height: 19px;
    padding: 15px 45px;
    font-weight: 300;
    border: 0;
    cursor: pointer;
    background: #FEBF2E;
    text-transform: uppercase;
    position: relative;
    height: 50px;
    transition: .3s ease all;

    &:before {
        content: '';
        display: block;
        position: absolute;
        border-top: 25px solid transparent;
        border-right: 16px solid #FEBF2E;
        border-bottom: 25px solid transparent;
        left: -16px;
        top: 0;
        transition: .3s ease all;
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        border-top: 25px solid transparent;
        border-left: 16px solid #FEBF2E;
        border-bottom: 25px solid transparent;
        right: -16px;
        top: 0;
        transition: .3s ease all;
    }

    &:hover {
        background: #ffde25;
        cursor: pointer;
        &:before {
            border-right: 16px solid #ffde25;
        }
        &:after {
            border-left: 16px solid #ffde25;
        }
    }

    &:disabled {
        opacity: .7;
        &:hover {
            cursor: not-allowed;
            background: #FEBF2E;
            &:before {
                border-right: 16px solid #FEBF2E;
            }
            &:after {
                border-left: 16px solid #FEBF2E;
            }
        }
    }

    @include media-breakpoint-down(md) {
        font-size: 14px;
        padding: 8px 30px 6px 30px;
    }
}

.btn-bordered {
    display: inline-block;
    text-decoration: none !important;
    border: 2px solid white;
    color: white;
    text-align: center;
    font-weight: 300;
    margin: 0;
    position: relative;
    border-radius: 50px;
    font-size: 18px;
    line-height: 18px;
    padding: 9px 40px 9px;
    
    
    @include media-breakpoint-down(md) {
        font-size: 14px;
        padding: 8px 40px 6px 40px;
    }

    &:hover {
        background: white;
        text-decoration: none;
        color: #2b317c;
        cursor: pointer;
    }
    &:disabled {
        opacity: .5;
    }
}

.btn-bordered--dark {
    color: #2b317c;
    border: 2px solid #2b317c;

    &:hover {
        background: #2b317c;
        color: white;
    }
}

.btn-more,
#mse2_mfilter .btn_more {
