.photo-slider {
    position: relative;
    z-index: 1;
}

.photo-slider__slide {
    height: 580px;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    position: relative;
    @include media-breakpoint-down(md) {
        height: 430px;
        background-position: right
    }
}
.photo-slider--style-2{

    .photo-slider__slide {
        border-radius: .4em;
        background-position: center;
        @include media-breakpoint-up(xl) {
            height: 705px;
        }
    }
	.photo-slider__controls {
		@include media-breakpoint-down(lg) {
			right: 30px;
		}
	}
}
.photo-slider__slide-label {
    position: relative;
    padding-left: 20px;
    padding-right: 20px;
    flex: 0 0 100%;
    max-width: 100%;

    @include media-breakpoint-up(xl) {
        flex: 0 0 54%;
        max-width: 54%;
    }
}

.photo-slider__caption{
    
    @include media-breakpoint-down(md) {
        position: absolute;
        width: 100%;
        height: auto;
        bottom: 0;
        padding: 15px 55px;
        background: linear-gradient(91.28deg,rgba(57, 50, 180, .85) 1.65%,rgba(90, 54, 189, .85) 98.35%);
        color: white;

        .btn {
            margin-top: 10px;
        }

        p {
            margin: 0;
        }
    }
    @include media-breakpoint-up(lg) {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;

        .container {
            height: 100%;

            .row {
                height: 100%;
            }
        }
    }
    @include media-breakpoint-down(sm) {
        padding: 25px 10px;
    }
}


.photo-slider__caption-bg{


    @include media-breakpoint-down(lg) {
        width: auto;
    }
    @include media-breakpoint-up(lg) {
        display: block;
        height: 100%;
        width: 65%;
        position: absolute;
        top: 0;
        left: -128px;
        transform: skew(-27deg);
        background: linear-gradient(91.28deg, #3932B4 1.65%, #5A36BD 98.35%);
        opacity: .85;
    }
    @include media-breakpoint-up(xl) {
        width: 105%;
    }
}

@include media-breakpoint-up(lg) {
    .photo-slider__caption-text {
        position: relative;
        z-index: 2;
        color: white;

        p {
            font-size: 24px;
            line-height: 1.5;
            font-weight: 300;
            padding-right: 200px;
        }

        .btn {
            margin-top: 40px;
        }
    }

    .photo-slider__caption-text-title {
        font-size: 36px;
        line-height: 50px;
        margin-bottom: 20px;

        b, strong {
            display: inline-block;
            font-weight: 800;
            font-size: 51px;
        }

    }
}

@include media-breakpoint-up(lg) {
    .photo-slider__caption-bg--small {
        bottom: -1px;
        height: 28%;
        top: auto;
    }
    .photo-slider__caption-text--small {
        position: absolute;
        bottom: 0;
        height: 28%;
        display: flex;
        align-items: center;

        p {
            font-size: 20px;
            line-height: 1.333;
            font-weight: 300;
            margin-left: 0;
            padding-right: 180px;
        }

    }
}

@include media-breakpoint-up(xl) {
    .photo-slider__caption-text--small {
        height: 24%;

        p {
            font-size: 24px;

            margin-left: 48px;
            padding-right: 140px;
        }

    }
}

.photo-slider__caption-text-title b, .photo-slider__caption-text-title strong {
    @include media-breakpoint-down(lg) {
        display: block;
    }
}

.photo-slider__caption-text p{
    @include media-breakpoint-down(lg) {
        max-width: 650px;
    } 
}

.photo-slider__controls {
    display: none;
    height: 60px;
    white-space: nowrap;
    z-index: 3;
    align-items: center;
    padding: 0 100px;
    position: absolute;
    bottom: 0;
    right: 100px;
    @include media-breakpoint-down(lg) {
        right: 0;
    }
    @include media-breakpoint-up(lg) {
        display: inline-flex;
    }

    &:before {
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transform: skew(-30deg);
        background: linear-gradient(91.99deg, rgba(57, 50, 180, .85) 1.65%, rgba(90, 54, 189, .85) 98.35%);
    }

    .owl-prev,
    .owl-next {
        width: 61px;
        height: 80px;
        top: 0;
        background: url('data:image/svg+xml;base64,') center no-repeat !important;
        background-size: contain !important;
        display: inline-block;
        z-index: 4;
        outline: none;
        border: none;
        position: absolute;
        transition: all .35s ease;
        &:hover {
            opacity: .7;
            cursor: pointer;
        }
        &:before,
        &:after {
            content: '';
            position: absolute;
            left: 10px;
            top: 50%;
            width: 10px;
            height: 2px;
            background-color: #ffffff;
            display: inline-block;

        }
        &:before{
            transform: rotate(-42deg);
            top: calc(50% - 4px);
        }
        &:after{
            transform: rotate(42deg);
            top: calc(50% + 4px);
        }
    }

    .owl-prev {
        left: 40px;
        top: -9px;
    }

    .owl-next {
        top: -9px;
        right: 40px;
        transform: rotate(180deg);
    }
}

.photo-slider__controls-dots {
    margin-top: 4px;
    position: relative;
    z-index: 5;
    text-align: center;
    line-height: 20px;
    .owl-dot {
        width: 16px;
        height: 16px;
        margin: 0 18px;
        border: 2px solid #FFFFFF;
        background: transparent;
        box-sizing: border-box;
        border-radius: 50px;
        display: inline-block;
        &.active {
            background: white;
        }
        &:hover {
            cursor: pointer;
            border-color: #2B317C;
        }
    }
}