::-webkit-input-placeholder {
    color: #92acb6;
}

:-moz-placeholder {
    color: #92acb6;
}

::-moz-placeholder {
    color: #92acb6;
}

:-ms-input-placeholder {
    color: #92acb6;
}

:focus::-webkit-input-placeholder {
    color: transparent !important;
}

:focus::-moz-placeholder {
    color: transparent !important;
}

:focus:-moz-placeholder {
    color: transparent !important;
}

:focus:-ms-input-placeholder {
    color: transparent !important;
}


input[type=checkbox],
input[type=radio] {
    font-size: 30px;
}

.required.error {
    color: red;
}

input[name=address] {
    position: absolute;
    left: -10000px;
}

.form__input,
.form__textarea {
    width: 100%;
    font-size: 24px;
    display: block;
    padding: 0 0 5px 0;
    outline: 0;
    margin-bottom: 30px;
    font-weight: 300;
    color: white;
    border: none;
    border-bottom: 2px solid white;
    background: transparent;
    resize: none;
    border-radius: 0;

    &.error {
        border-bottom: 2px solid brown;
    }

    &::-webkit-input-placeholder {
        color: white;
    }

    &:-moz-placeholder {
        color: white;
    }

    &::-moz-placeholder {
        color: white;
    }

    &:-ms-input-placeholder {
        color: white;
    }
    &--mb-50{
        margin-bottom: 50px;
    }
}

.form__input--dark {
    color: #2B317C;
    border-color: #2B317C;

    &::-webkit-input-placeholder {
        color: #2B317C;
    }

    &:-moz-placeholder {
        color: #2B317C;
    }

    &::-moz-placeholder {
        color: #2B317C;
    }

    &:-ms-input-placeholder {
        color: #2B317C;
    }
}

.form__input--bordered,
.form__textarea--bordered {
    border: 1px solid #2a9ed9;
}

.form__textarea {
    height: 50px;
    line-height: 24px;
}

.form__checkbox {

    span {
        display: inline-block;
        content: '';
        height: 18px;
        width: 18px;
        border: 2px solid white;
        margin-right: 9px;
        position: relative;
        border-radius: .3em;
    }

    input[type=checkbox] {
        display: none;
        margin-top: 2px;
    }

    input[type=checkbox]:checked + span {

        &:after {
            background: white;
            display: block;
            content: '';
            width: calc(100% - 4px);
            height: calc(100% - 4px);
            position: absolute;
            left: 2px;
            top: 2px;
        }
    }
}

.form__politics {
    margin-bottom: 25px;

    p {
        text-align: left;
        color: white;
        font-weight: 300;
        font-size: 13px;
        line-height: 18px;
        margin: 3px 0 0 0;

        a {
            color: white;
            text-decoration: underline;
            line-height: 1.5;
            &:hover {
                text-decoration: none;
            }
        }
    }

    &:hover {
        cursor: pointer;
    }

}

.form__checkbox--dark {
    span {
        border-color: #2B317C;
    }
    input[type=checkbox]:checked + span {

        &:after {
            background: #2B317C;
        }
    }
}

.form__politics--dark {
    p {
        color: #271f5e;

        a {
            color: #271f5e;
        }
    }
}

.form--border {
    .content {
        padding-top: 30px;

        @include media-breakpoint-down(sm) {
            padding-top: 50px;
        }
    }
}

.form-big {
    background: linear-gradient(93.64deg, #3932B4 1.65%, #5A36BD 98.35%);
    border-radius: 1em;
    padding: 100px 0;

    @include media-breakpoint-down(md) {
        padding: 60px 0 40px 0;
    }
    @include media-breakpoint-down(sm) {
        padding: 60px 10px 40px;
    }

}

.form-big__wrapper {
    position: relative;
    padding: 90px 0 85px;
    border-left: 3px solid #fff;
    border-right: 3px solid #fff;
    border-radius: .5em;
    margin: 0 auto;

    @include media-breakpoint-up(md) {
        max-width: 80%;

    }
    @include media-breakpoint-up(xl) {
        max-width: 100%;
    }
    &:before {
        content: '';
        display: block;
        height: 3px;
        width: 100%;
        background: radial-gradient(circle, transparent 50%, white 50%);
        position: absolute;
        top: 0;

        @include media-breakpoint-down(lg) {
            background: radial-gradient(circle, transparent 50%, white 50%);
        }

        @include media-breakpoint-down(md) {
            background: radial-gradient(circle, transparent 60%, white 60%);
        }

        @include media-breakpoint-down(sm) {
            background: radial-gradient(circle, transparent 80%, white 80%);
        }

    }

    &:after {
        content: '';
        display: block;
        height: 3px;
        width: 100%;
        background: radial-gradient(circle, transparent 25%, white 25%);
        position: absolute;
        bottom: 0;

        @include media-breakpoint-down(sm) {
            background: -moz-radial-gradient(circle,transparent 55%,#fff 30%);
            background: radial-gradient(circle,transparent 55%,#fff 0);
        }

    }
}

.form-big__icon-bottom {
    content: '';
    display: block;
    height: 30px;
    background-image: url(data:image/svg+xml;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    bottom: -15px;
    width: 100%;

}

.form-big__title {
    font-family: "Gotham Pro";
    color: #fff;
    font-size: 36px;
    line-height: 44px;
    font-weight: bold;
    text-transform: uppercase;
    top: -35px;
    position: absolute;
    width: 100%;
    text-align: center;

    @include media-breakpoint-down(md) {
        font-size: 18px;
        line-height: 28px;
        margin-top: 14px;
    }

    span {
        font-weight: 300;
        font-size: 24px;
        line-height: 30px;
        margin-top: 0;
        display: block;

        @include media-breakpoint-down(md) {
            font-size: 16px;
            margin-top: -2px;
        }
    }

}

.form-small__wrapper {
    position: relative;
    padding: 75px 74px 45px;
    border-left: 3px solid #fff;
    border-right: 3px solid #fff;
    border-top: 3px solid #fff;
    border-radius: .3em;
    @include media-breakpoint-down(md) {
        padding: 35px 25px 30px;
    }

    &:after {
        content: '';
        display: block;
        height: 3px;
        width: 100%;
        background: radial-gradient(circle, transparent 30%, white 30%);
        position: absolute;
        bottom: 0;
        left: 0;
        @include media-breakpoint-down(sm) {
            background: -moz-radial-gradient(circle,transparent 55%,#fff 30%);
            background: radial-gradient(circle,transparent 55%,#fff 0);
        }
    }
}

.form-small__icon-bottom {
    content: '';
    display: block;
    height: 30px;
    background-image: url(data:image/svg+xml;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    bottom: -15px;
    width: 100%;
    left: 0;
}

