.header {
    position: relative;
    //background-color: #282a52;
    padding-top: 15px;
    &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        background-color: rgba(255,255,255, .1);
        width: 100%;
        height: 1px;
    }
}
.header__logo {
    display: flex;
    align-items: center;
    //padding: 45px 0 17px 0;
	margin-right: 5%;
    a {
        display: block;
        margin-right: 10px;
    }
    img {
        width: 140px;
    }
}

.header__logo-description {
    color: #fff;
    font-size: 11px;
    line-height: 1.5;
    padding-top: 5px;
    max-width: 158px;
    text-transform: uppercase;
    text-align: center;


    span {
        font-size: 13px;
    }
}

.header__links {
    position: relative;
    > a {
        font-size: 14px;
        font-weight: bold;
        display: block;
        position: relative;
        color: #ffffff;
        &:after {
            border: 5px solid transparent;
            border-top: 6px solid #fff;
            content: "";
            position: absolute;
            right: 14px;
            top: 11px;
        }
        &:hover {
            text-decoration: none;
        }
    }
    > span {
        display: block;
        font-size: 11px;
        font-weight: 300;
        color: #fff;
    }
}

.header__links-content {
    &:before{
        content: '';
        position: absolute;
        left: -moz-calc(50% - 12px);
        left: calc(50% - 12px);
        top: -23px;
        border: 12px solid transparent;
        border-bottom: 12px solid white;
    }
    display: none;
    position: absolute;
    -moz-box-shadow: 0 0 20px rgba(0,0,0,.05);
    box-shadow: 0 0 20px rgba(0,0,0,.05);
    background: white;
    width: 100%;
    padding: 15px 7px 5px;
    margin-top: 20px;
    z-index: 10;
    a {
        display: inline-block;
        font-size: 16px;
        color: rgb(43, 49, 124);
        text-decoration: none;
        margin-bottom: 15px;
        font-weight: 300;
        padding-bottom: 0px;
        border-bottom: 1px solid #2b317c;
        line-height: 22px;

        &:hover {
            text-decoration: none;
            border-bottom: 1px solid transparent;
        }
    }

}

.header__phone {
    font-size: 24px;
    margin-right: 6%;
    span {
        font-weight: 600;
    }
    
    a {
        text-decoration: none;
        color: #ffffff;
        &:hover {
            text-decoration: none;
        }
    }
}

.header__nav {
    position: relative;
    margin: 15px 0 0 0;
    width: 100%;
    background: linear-gradient(143.23deg, #3932B4 1.65%, #5A36BD 98.35%);
    &:after{
        content: '';
        display: block;
        height: 5px;
        width: 100%;
        background-color: #ffffff;
    }
    ul {
        margin: 0;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
        list-style: none;
        > li {
            position: relative;
            > a {
                color: #fff;
                display: block;
                font-size: 20px;
                padding: 10px 8px 8px;
                text-align: center;
                text-decoration: none;
                font-weight: 300;
                &:hover,
                &:focus{
                    &:after {
                        border: 9px solid transparent;
                        border-bottom: 7px solid white;
                        content: "";
                        left: calc(50% - 9px);
                        position: absolute;
                        bottom: -1px;
                    }
                }
            }
            &.active {
                &:after {
                    border: 9px solid transparent;
                    border-bottom: 7px solid white;
                    content: "";
                    left: calc(50% - 9px);
                    position: absolute;
                    bottom: -1px;
                }
            }
        }
    }
}

.header-mobile--sticky {
    .inner-wrapper-sticky {
        z-index: 20;
    }
}

.header-mobile {
    background: linear-gradient(143.23deg, #3932B4 1.65%, #5A36BD 98.35%);
    color: white;
    height: 55px;
    border-bottom: 5px solid white;
    
    @include media-breakpoint-down(sm) {
        border: none;
        height: 50px;
    }
}

.header-mobile__logo {
    img {
        height: 41px;
        @include media-breakpoint-down(md) {
            height: 28px;
        }
    }
}

.header-mobile__phone {
    a {
        color: white;
        font-size: 18px;
        font-weight: 300;
        padding-top: 2px;
        display: block;
        
        &:hover {
            text-decoration: none;
        }

        span {
            font-weight: 600;
        }
    }
}

.header-mobile__nav-icon {

    width: 26px;
    height: 16px;
    float: left;
    position: relative;
    margin: 17px 0;
    transform: rotate(0deg);
    cursor: pointer;

    span {
        display: block;
        position: absolute;
        height: 2px;
        width: 100%;
        background: rgba(255, 255, 255, 1);
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: .25s ease-in-out;
        &:nth-child(1) {
            top: 0px;
        }
        &:nth-child(2),
        &:nth-child(3) {
            top: 7px;
        }
        &:nth-child(4) {
            top: 14px;
        }
    }
    &.open span:nth-child(2) {
        transform: rotate(45deg);
    }
    &.open span:nth-child(3) {
        transform: rotate(-45deg);
    }
    &.open span:nth-child(1),
    &.open span:nth-child(4) {
        top: 8px;
        opacity: 0;
        left: 50%;
        width: 0%;
    }
}

.header-mobile__nav {
    z-index: 100;
    display: none;
    position: absolute;
    background: linear-gradient(143.23deg, #3932B4 1.65%, #5A36BD 98.35%);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    width: 100%;
    left: 0;
    top: 55px;
   // padding: 10px 55px;
    text-align: center;
    @include media-breakpoint-down(sm) {
        top: 50px;
    }
    p {
        padding: 5px 0;
        margin: 0;
        font-size: 16px;
        line-height: 24px;
    }
    ul {
        list-style: none;
        padding: 0;
        margin: 30px 0;
        li {
            margin: 0;
            padding: 0;
        }
        a {
            display: block;
            color: #fff;
            text-decoration: none;
            margin-bottom: 15px;
            font-size: 20px;
            font-weight: 300;
            &:hover,
            &:active {
                opacity: .5;
            }
        }
    }
}

.header-sticky {
    background: linear-gradient(143.23deg, #3932B4 1.65%, #5A36BD 98.35%);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
    display: none;
    -moz-box-shadow: 0 0 20px rgba(0,0,0,.05);
    box-shadow: 0 0 20px rgba(0,0,0,.05);
    @include media-breakpoint-down(md) {
        display: none !important;
    }
}

.header-sticky__nav {
    flex-grow: 1;
    ul {
        list-style: none;
        margin: 0;

        li {
            //display: none;
            display: inline-block;

            &:nth-child(-n+5){
                //display: inline-block;
            }

            a {
                color: #fff;
                display: block;
                font-size: 16px;
                padding: 10px 8px 8px;
                text-align: center;
                text-decoration: none;
                font-weight: 300;

                &:hover {
                    opacity: .5;
                }
            }
        }

    }
}

.header-sticky__contacts {
    .btn {
        margin: 0 0 0 30px;
    }
}

.header-sticky__phone {
    a {
        font-size: 20px;
        color: white;
        line-height: 1;
        margin-top: 8px;
        display: inline-block;

        &:hover {
            text-decoration: none;
        }

         span {
             font-weight: bold;
         }
    }
}

@include media-breakpoint-up(sm) {
    .header-mobile{
        &__nav{
            text-align: left;
        }
    }
}
@include media-breakpoint-up(xl) {
    .header{
        padding-top: 45px;
        &__logo{
            margin-right: 13%;
            a {
                margin-right: 24px;
            }
            img {
                width: 199px;
            }
        }
        &__logo-description {
            font-size: 13px;
            span {
                font-size: 16px;
            }
        }
        &__phone{
            font-size: 34px;
            margin-right: 10%;
        }

        &__links{
            > a {
                font-size: 24px;
            }
            > span {
                font-size: 18px;
            }
        }
        &__links-content{
            padding: 30px 30px 15px;
            a{
                font-size: 20px;
            }
        }
        &__nav{
            margin: 40px 0 0;
            a {
                font-size: 20px;
                padding: 13px 11px 11px;
            }

        }

    }
    .header-sticky{
        &__phone{
            a {
                font-size: 26px;
            }
        }
        &__nav{
            a{
                font-size: 20px;
                padding: 13px 11px 11px;
            }
        }
    }

}