.clients {
    padding: 0 0 30px;

    @include media-breakpoint-up(lg) {
        padding: 0 0 124px;
    }
}
.clients__item {
    position: relative;
    padding: 40px 0;
    text-align: center;
    margin: 0 3px 30px;
    width: 100%;
    height: 130px;
    display: block;
    &:before {
        content: "";
        display: block;
        position: absolute;
        left: -3px;
        right: -3px;
        top: -3px;
        bottom: -3px;
        background: -moz- oldlinear-gradient(358.66deg,#3932b4 1.65%,#5a36bd 98.35%);
        background: linear-gradient(91.34deg,#3932b4 1.65%,#5a36bd 98.35%);
        -moz-border-radius: 5px;
        border-radius: 5px;
        -moz-transition: all .3s ease;
        transition: all .3s ease;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: #282a52;
    }
    img {
        position: relative;
        z-index: 1;
        display: inline-block;
        max-width: 140px;
    }

    &:hover {
        opacity: .5;
    }

}