.portfolio-wrap {
    margin-bottom: 84px;
}
.portfolio__item {
    position: relative;
    text-decoration: none !important;
    display: block;
    margin: 0 0 40px;
    transition: .3s all;
    border-radius: 5px;
    background: #232A49;
    &:before {
        content: '';
        display: block;
        position: absolute;
        left: -3px;
        right: -3px;
        top: -3px;
        bottom: -3px;

        background: linear-gradient(91.34deg, #3932B4 1.65%, #5A36BD 98.35%);
        border-radius: 5px;
        transition: all .3s ease;

        opacity: 0;
        visibility: hidden;
    }
    &:focus,
    &:hover {
        transform: translate(0, -5px);
        .portfolio__item-title{
            color: #ffffff;
            span {
                color: #ffffff;
            }
        }
        &:before {
            opacity: 1;
            visibility: visible;
            color: #ffffff;
        }

    }


}

.portfolio__item-image {
    background-repeat: no-repeat;
    background-size: cover;
    height: 420px;
    text-align: center;
    position: relative;
    border-top-left-radius: .4em;
    border-top-right-radius: .4em;
    @include media-breakpoint-down(sm) {
        height: 350px;
        border-radius: .4em;
    }

    &--overlay {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            background: #232A49;
            opacity: 0.4;
        }
    }
}

.portfolio__item-logo {
    display: block;
    width: 100%;
    position: absolute;
    bottom: 15px;
    text-align: center;
    z-index: 2;

    img {
        display: block;
        margin: 0 auto;
        width: 200px !important;
    }
}

.portfolio__item-title {
    position: relative;
    z-index: 2;
    //background: #232A49;
    height: 235px;
    max-height: 235px;
    color: #9498A4;
    font-size: 24px !important;
    font-weight: 300 !important;
    text-transform: none !important;
    display: block;
    margin: 0 !important;
    padding: 40px 40px 30px 40px;
    line-height: 34px;
    overflow: hidden;

    span {
        color: #9498A4;
        display: block;
        overflow: hidden;
        height: 100%;
    }

    @include media-breakpoint-down(md) {
        height: 185px;
        max-height: none;
        padding: 20px 30px;
        font-size: 20px !important;
        line-height: 26px !important;
    }
}

.portfolio__slider {
    margin-bottom: 40px;
    @include media-breakpoint-down(sm) {
        margin: 0 10px;

        .portfolio__item-title {
            display: none;
        }
        .portfolio__item {
            margin-bottom: 0;
        }
    }

    .owl-prev,
    .owl-next {
        width: 25px;
        height: 90px;
        position: absolute;
        top: calc(50% - 45px);
        background: url(data:image/png;base64,)center no-repeat !important;
        background-size: contain !important;
        display: inline-block;
        z-index: 1;
        outline: none;
        @include media-breakpoint-down(sm) {
             display: none;
        }
        &:hover {
            opacity: .6;
        }

    }

    .owl-prev {
        left: -35px;

        //@include media-breakpoint-down(sm) {
        //    left: -45px;
        //    width: 20px;
        //}
    }

    .owl-next {
        right: -35px;
        transform: rotate(180deg);
        //@include media-breakpoint-down(sm) {
        //    right: -35px;
        //    //width: 20px;
        //}
    }

}

.portfolio__item-text {
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    color: #271f5e;

    .text-line {
        span {
            font-weight: bold;
            text-transform: uppercase;
            float: left;

            @include media-breakpoint-down(md) {
                float: none;
            }
        }

        .line-right {
            padding-left: 100px;
            margin-bottom: 22px;

            @include media-breakpoint-down(md) {
                padding-left: 0;
            }
        }
    }
}

.portfolio__video-item {
    display: block;
    height: 150px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    cursor: pointer;
    margin-top: 15px;

    &:after {
        display: block;
        content: '';
        height: 40px;
        width: 40px;
        background: #ffc61a 12px center no-repeat url('data:image/svg+xml;utf8;base64,');
        background-size: 55%;
        position: absolute;
        top: calc(50% - 20px);
        left: calc(50% - 20px);
        border-radius: 50%;
    }

    &:before {
        display: block;
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: transparent;
        transition: all ease .5s;
    }

    &:hover {
        &:before {
            background: rgba(97, 183, 227, .7);
            transition: all ease .5s;
        }
    }

}

.portfolio__video-item-title {
    text-align: center;
    margin-top: 10px;
    font-size: 16px;
}

.portfolio__info {
    b {
        font-size: 23px;
        @include media-breakpoint-down(lg) {
            font-size: 20px;
        }
    }
}