.vacancies__item {
    margin: 10px 0;
    text-decoration: none !important;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include media-breakpoint-down(md) {
        padding: 0 20px;
    }
}

.vacancies__item-image {
    display: block;
    vertical-align: text-top;
    flex: 0 0 100%;
    max-width: 100%;
    border-radius: .25em;
    overflow: hidden;

    img {
        width: 100%;
    }
    @include media-breakpoint-up(md) {
        flex: 0 0 45%;
        max-width: 45%;
    }
}

.vacancies__item-description {
    margin-top: 40px;
    flex: 0 0 100%;
    max-width: 100%;
    @include media-breakpoint-up(md) {
        flex: 0 0 52%;
        max-width: 52%;
    }
    h4 {
        margin-top: 0;
        font-size: 24px;
        margin-bottom: 16px;

    }
    p {
        margin: 0 0 20px;
    }
    strong {
        font-size: 24px;
    }
}

.vacancies__vk-block {
    border: rgba(230, 230, 230, 1) 1px solid;
}

.vacancies__text-block {
    height: 100%;
    padding: 40px;
    margin-bottom: 0;

    h2 {
        margin-top: 0;
    }

    @include media-breakpoint-down(md) {
        margin-top: 40px;
    }

}

.vacancies-slider {

    .owl-prev,
    .owl-next {
        width: 25px;
        height: 90px;
        position: absolute;
        top: 50%;
        background: url(data:image/png;base64,)center no-repeat !important;
        background-size: contain !important;
        display: inline-block;
        z-index: 1;
        outline: none;
        transform: translate(0, -100%);
        &:hover {
            opacity: .6;
        }

    }

    .owl-prev {
        left: -18px;
        @include media-breakpoint-up(md) {
            left: -35px;
        }
    }

    .owl-next {
        right: -18px;
        transform: translate(0, -100%) rotate(180deg);
        @include media-breakpoint-up(md) {
            right: -35px;
        }

    }
}

