.vacancy-big {
    border-radius: .25em .25em 0px 0px;
    overflow: hidden;
    margin-bottom: 60px;
    &__preview {
        position: relative;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            background: #232A49;
            opacity: 0.65;
        }

        img {
            max-width: 100%
        }
    }

    &__caption {
        position: absolute;
        right: 0;
        bottom: 8%;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding:0 5%;
        svg {
            transition: all .35s ease;
        }
    &.active{
        svg {
            transform: rotate(-180deg);
        }
    }
    }

    &__title {
        font: 900 18px 'Gotham Pro';
        color: #ffffff;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        @include media-breakpoint-up(lg) {
            font-size: 26px;

        }
    }

    &__body{
        display: none;
        background: #F0F3F9;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
        padding: 20px 7% 15px;
        color: #232A49;
        @include media-breakpoint-up(lg) {
            padding: 55px 5% 45px;
        }
    }

    &__sub-caption{
        font-size: 18px;
        @include media-breakpoint-up(lg) {
            font-size: 24px;

        }
    }
}