.models__item {
    margin: 20px 0;
    position: relative;
    transition: .3s all;
    -moz-box-shadow: 0 0 20px rgba(0,0,0,.05);
    box-shadow: 0 0 20px rgba(0,0,0,.05);
    @include media-breakpoint-up(lg) {
        &:hover {
            box-shadow: 0px 15px 40px rgba(38, 156, 216, 0.4);
            margin: 10px 0 30px 0;
        }
    }
}

.models__item-image {
    height: 100%;
    background-position: center top;
    background-size: cover;
}

.models__item-card {
    color: #fff;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 550px;
    position: relative;
    margin: 0 auto;
    overflow: hidden;
    position: relative;

    @include media-breakpoint-down(sm) {
        height: 350px;
    }

    &:before {
        z-index: 1;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 70%;
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(38, 156, 216, 1) 85%, rgba(38, 156, 216, 1) 100%);
    }
}

.models__item-info {
    position: absolute;
    bottom: 0;
    display: block;
    color: #fff;
    width: 100%;
    padding: 40px 0 30px 0;
    transition: all .3s ease 0s;
    z-index: 2;
}

.models__item-caption {
    font-size: 26px;
    text-align: center;
    font-weight: 600;
}

.models__filter-button {
    @include media-breakpoint-down(lg) {
        margin-bottom: 15px;
    }
}

.model__params {
}

.model__params-item {
    display: flex;
    padding: 0 0 6px 0;
}

.model__params-name {
    font-size: 18px;
    color: #2b317c;
}

.model__params-sep {
    flex-grow: 1;
    border-bottom: 1px dotted #2b317c;
    margin: 0 5px 6px 5px;
}

.model__params-value {
    font-size: 18px;
    font-weight: 600;
    color: #2b317c;
}

.model__gallery-item {
    margin-bottom: 40px;

    a {
        opacity: .9;

        &:hover {
            opacity: 1;
        }
    }

    img {
        width: 100%;
    }
}

.model__info-list {
    box-sizing: border-box;
    padding: 40px 80px 40px 40px;
    margin-bottom: 0;

    @include media-breakpoint-only(xl) {
        min-height: 580px;
    }

    h5 {
        color: #2b317c;
        font-size: 24px;
        font-weight: bold;
        margin-top: 0;
        text-transform: uppercase;
    }
    ul {
        color: #2b317c;
        a {
            color: #2b317c;
        }
    }
}

.model__slider {

    .owl-prev,
    .owl-next {
        width: 28px;
        height: 90px;
        position: absolute;
        top: calc(50% - 45px);
        background: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNjIiIGhlaWdodD0iMTg0IiB2aWV3Qm94PSIwIDAgNjIgMTg0IiBmaWxsPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPjxwYXRoIGQ9Ik0yOCAwTDAgNDVMMjggOTAiIHRyYW5zZm9ybT0idHJhbnNsYXRlKDQgMikgc2NhbGUoMikiIHN0cm9rZT0iIzI2OUNEOCIgc3Ryb2tlLXdpZHRoPSIyIi8+PC9zdmc+') center no-repeat !important;
        background-size: contain !important;
        display: inline-block;
        z-index: 1;
        outline: none;

        &:hover {
            opacity: .6;
        }

    }

    .owl-prev {
        left: -50px;
    }

    .owl-next {
        right: -50px;
        transform: rotate(180deg);
    }

}

.model__info {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    padding: 50px 40px 20px 40px;
    margin-bottom: 40px;

    @include media-breakpoint-only(xl) {
        min-height: 547px;
    }

    @include media-breakpoint-down(sm) {
        padding: 30px;
    }

    .btn {
        margin: 20px auto 0 auto;
        display: block;
    }

}