.prices__item-wrap {
    height: 100%;
    padding: 20px 0;
}
.prices__item {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    text-decoration: none !important;
    display: flex;
    flex-direction: column;
    height: 100%;
    transition: .3s all ease;
    background: #232A49;
    border-radius: .3em;
    &:before {
        content: '';
        display: block;
        position: absolute;
        left: -3px;
        right: -3px;
        top: -3px;
        bottom: -3px;

        background: linear-gradient(91.34deg, #3932B4 1.65%, #5A36BD 98.35%);
        border-radius: 5px;
        transition: all .3s ease;

        opacity: 0;
        visibility: hidden;
    }
    &:focus,
    &:hover {
        transform: translate(0, -5px);
        .prices__item-info{
            color: #ffffff;
        }
        &:before {
            opacity: 1;
            visibility: visible;
        }

    }

}

.prices__item-image {
    background-repeat: no-repeat;
    background-size: cover;
    height: 423px;
    text-align: center;
    position: relative;
    border-radius: .3em .3em 0 0;
    @include media-breakpoint-down(sm) {
        height: 350px;
    }

    &:before {
        z-index: 1;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #232A49;
        opacity: 0.65;
    }
}

.prices__item-title {
    position: absolute;
    bottom: 0;
    display: block;
    text-align: center;
    z-index: 2;
    width: 100%;
    text-transform: none !important;
    margin-bottom: 25px;

    span {
        display: block;
        color: white;

        &:nth-child(1) {
            font: bold 26px 'Gotham Pro';
            text-transform: uppercase;
        }

        &:nth-child(2) {
            font-size: 24px;
            font-weight: 500;
        }

    }

    @include media-breakpoint-down(md) {
        height: auto;
        max-height: none;
        padding: 20px 30px;
        font-size: 20px !important;
        line-height: 26px !important;
    }
}

.prices__item-info {
    position: relative;
    color: #9498A4;
    display: flex;
    flex-direction: column;
	flex: 1;
    margin: 0 !important;
    padding: 40px 40px 30px 40px;
    line-height: 1.4;
    overflow: hidden;
    @include media-breakpoint-down(sm) {
        padding: 20px 20px;
    }

}

.prices__params {
    margin-bottom: 30px;
}

.prices__params-item {
    display: flex;
    line-height: 18px;
    margin-bottom: 10px;
}

.prices__params-name {
    font-size: 18px;

}

.prices__params-sep {
    flex-grow: 1;
    border-bottom: 1px dotted #9498A4;
    margin: 0 5px 3px 5px;
}

.prices__params-value {
    font-size: 18px;
    font-weight: 600;
    //color: #2b317c;
}
.prices__button-wrap {
    margin-top: auto;
}