.posts__item {
    position: relative;
    background: #232A49;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    text-decoration: none !important;
    display: block;
    margin: 20px 0;
    transition: .3s all;
    color: #9498A4;
    &:before {
        content: "";
        display: block;
        position: absolute;
        left: -3px;
        right: -3px;
        top: -3px;
        bottom: -3px;
        background: -moz- oldlinear-gradient(358.66deg,#3932b4 1.65%,#5a36bd 98.35%);
        background: linear-gradient(91.34deg,#3932b4 1.65%,#5a36bd 98.35%);
        -moz-border-radius: 5px;
        border-radius: 5px;
        -moz-transition: all .3s ease;
        transition: all .3s ease;
        opacity: 0;
        visibility: hidden;
    }
    &:hover,
    &:focus{
        color: #ffffff;
        cursor: pointer;
        transform: translateY(-5px);
        .posts__item-more {
            color: #ffffff;
        }
        .posts__item-date {
            .icon-calendar {
                opacity: 1;
            }
        }
        &:before {
            opacity: 1;
            visibility: visible;
        }
    }
}

.posts__item-info {
    position: relative;
    padding: 35px 40px 25px 40px;

    @include media-breakpoint-down(lg) {
        padding: 20px 20px 15px 20px;
    }
}

.posts__item-image {
    position: relative;
    height: 320px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;

    border-radius: .2em .2em 0px 0px;
    overflow: hidden;

    &:before {
        z-index: 1;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #232A49;
        opacity: 0.65;
    }
}

.posts__item-title {
    position: absolute;
    bottom: 0;
    display: block;
    z-index: 2;
    font-weight: bold;
    font-size: 36px;
    padding: 0 0 25px 40px;
    color: white;
    width: 69%;
    line-height: 1.2;

    @include media-breakpoint-down(lg) {
        font-size: 26px;
    }
    @include media-breakpoint-down(md) {
        font-size: 22px;
        width: 100%;
        padding: 0 20px 20px 20px;
    }

}

.posts__item-text {
    font-size: 24px;
    line-height: 1.3;
    margin-bottom: 20px;

    @include media-breakpoint-down(lg) {
        font-size: 18px;
    }

}

.posts__item-date {
    font-size: 24px;
    line-height: 1.4;
    display: inline-flex;
    align-items: center;

    .icon-calendar {
        opacity: .5;
    }

    @include media-breakpoint-down(lg) {
        font-size: 18px;
    }
}

.posts__item-more {
    font-size: 24px;
    color: #9498A4;

    @include media-breakpoint-down(lg) {
        font-size: 20px;
    }
}