.form-absolute{
	max-height: 454px;

	&__form {
		position: relative;
		z-index: 1;
		margin: 0 auto;


		transform: translate(0, -48%);

		//max-width: 985px;
		@include media-breakpoint-up(md) {
			max-width: 720px
		}
		@include media-breakpoint-up(lg) {
			max-width: 985px
		}
		@include media-breakpoint-up(xl) {
			max-width: 1370px;
		}
	}

}