.feedback__item {
    padding: 56px 7.4% 0;
    margin: 15px 0 0 0;
    justify-content: space-between;
    @include media-breakpoint-down(md) {
        margin: 0;
        padding: 0;
    }
}

.feedback__item-image {
    padding: 15px;
    position: relative;
    text-align: center;
    border-radius: 2px;
    border: 2px solid #ffffff;
    box-shadow: 0 0 0px 3px #3932b4;

    @include media-breakpoint-down(md) {
        margin: 0 auto;
    }

    @include media-breakpoint-up(lg) {
        flex: 0 0 25.4%;
        max-width: 25.4%;
    }
    @include media-breakpoint-up(xl) {
        flex: 0 0 22.4%;
        max-width: 22.4%;
    }

    a {
        display: block;
        position: relative;
    }

    img {
        vertical-align: middle;
        position: relative;
        z-index: 2;
    }

    &:before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        background: linear-gradient(91.45deg, #3932B4 1.68%, #5A36BD 51.68%);
        border-radius: 2px;


    }
}

.feedback__item-content {
    padding: 15px 0 0 10px;
    text-align: left;

    @include media-breakpoint-up(lg) {
        flex: 0 0 70%;
        max-width: 70%;
    }
    @include media-breakpoint-up(xl) {
        padding: 30px 0 0 10px;
    }
}

.feedback__item-title {
    color: #232A49;
    font-size: 48px;
    line-height: 1.2;
    font-weight: normal;
    margin-bottom: 25px;
    @include media-breakpoint-down(lg) {
        font-size: 42px;
        margin-bottom: 15px;
    }
    @include media-breakpoint-down(md) {
        font-size: 36px;
        margin-top: 20px;
    }
}

.feedback__item-text {
    color: #232A49;
    font-size: 24px;
    line-height: 1.3;
    margin-bottom: 25px;
    @include media-breakpoint-down(lg) {
        font-size: 20px;
    }
    @include media-breakpoint-down(sm) {
        font-size: 16px;
    }
}

.feedback__item-sign {
    color:#232A49;
    font-size: 24px;
    line-height: 1.33;
    margin-bottom: 0px;
    font-weight: bold;
    @include media-breakpoint-down(lg) {
        font-size: 20px;
    }
    @include media-breakpoint-down(sm) {
        font-size: 18px;
    }
}

.feedback__feedback-carousel {
    margin-bottom: -40px;

    @include media-breakpoint-down(sm) {
        margin: 0 10px;
    }

    .owl-prev,
    .owl-next {
        width: 28px;
        height: 90px;
        position: absolute;
        top: 210px;
        background: url(data:image/svg+xml;base64,) center no-repeat !important;
        background-size: contain !important;
        display: inline-block;
        z-index: 1;
        outline: none;

        &:hover {
            opacity: .6;
        }

        @include media-breakpoint-down(sm) {
            top: 160px;
        }

    }

    .owl-prev {
        left: 0px;

        @include media-breakpoint-down(sm) {
            left: -18px;
            width: 20px;
        }
    }

    .owl-next {
        right: 0px;
        transform: rotate(180deg);
        @include media-breakpoint-down(sm) {
            right: -18px;
            width: 20px;
        }
    }

}
.feedback__feedback-carousel--arrow-0 {
    .owl-prev {
        left: 0px;
    }
    .owl-next {
        right: 0px;
    }
}
.feedback__item-content{
    &--white {
        color: #ffffff;

        .feedback__item-title,
        .feedback__item-text,
        .feedback__item-sign{

            color: #ffffff;
        }
    }
}