.blog__item-date p {
    margin: 0;
} 

.blog__item-image {
    img {
        width: 100%;
    }

    @include media-breakpoint-down(lg) {
        margin-bottom: 20px !important;
    }
    @include media-breakpoint-down(md) {
        margin-bottom: 10px !important;
    }
}

.blog__item-title {
    h5 {
        font-size: 24px;
        line-height: 28px;
        text-transform: none;
        font-weight: 500;
        margin: 10px 0;

        @include media-breakpoint-down(lg) {
            font-size: 18px;
            line-height: 22px;
            margin: 10px 0 0 0;
        }

        @include media-breakpoint-down(md) {
            margin: 5px 0 0 0;
        }

        a {
            color: #008ad1;
        }
    }
}

.blog__item-text {
    p {
        @include media-breakpoint-down(lg) {
            margin: 15px 0;
        }
        @include media-breakpoint-down(md) {
            margin: 10px 0;
        }
    }

}

.blog__item-url {

    p {
        margin: 0;
    }

    margin: 0 0 40px 0 !important;
    @include media-breakpoint-down(lg) {
        margin: 0 0 20px 0 !important;
    }
    @include media-breakpoint-down(md) {
        margin: 0 0 30px 0 !important;
    }
}

.blog__menu {
    //background: white;
    h3 {
        background-color: #24a0d4;
        color: #fff;
        font-size: 18px;
        padding: 13px 0 10px;
        text-align: center;
        text-transform: uppercase;
        margin: 0 0 2px 0;
    }
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }
    li {
        margin: 2px 0;
        a {
            background: #fff;
            border: 3px solid #fff;
            text-decoration: none;
            color: #008ad1;
            font-size: 20px;
            display: block;
            padding: 13px 12px;
            &:hover {
                color: #2f3587;
                border: 3px solid #2f3587;
            }
        }
    }
}

.blog__consult {
    margin-bottom: 20px;
    padding: 12px;
    background-image: -webkit-gradient(linear, 100% 0, 0 0, from(#0071b6), color-stop(.52, #269cd8), to(#0071b6));
    background-image: -webkit-linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    background-image: -moz-linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    background-image: -o-linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    background-image: linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    border-radius: 10px;
}

.blog__consult-person {
    padding-left: 90px
}

.blog__consult-photo {
    float: left;
    width: 75px;
    height: 80px;
    margin-left: -90px;
    border: 3px solid #fff;
    border-radius: 40px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .35)
}

.blog__consult-job {
    padding-top: 8px;
    font-size: 16px;
    line-height: 20px;
    color: #fff;
    font-weight: 700;
    margin: 0;
}

.blog__consult-name {
    padding-top: 20px;
    line-height: 18px;
    font-size: 18px;
    color: #fff;
    font-weight: 700;
}

.blog__consult-blockquote {
    position: relative;
    margin-top: 14px;
    padding: 12px 12px;
    background: #fff;
    border-radius: 10px;
    &:after {
        content: '';
        position: absolute;
        left: 65px;
        top: -20px;
        width: 17px;
        height: 20px;
    }
}

.blog__consult-needhelp {
    color: #000;
    font-size: 13.5px;
    font-weight: 700;
    line-height: 16px
}

.blog__consult-call {
    color: grey;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    margin: 5px 0
}

.blog__consult-phone {
    color: #2f3587;
    display: block;
    font-size: 20px;
    font-weight: 700;
    margin-top: 5px;
    white-space: nowrap
}

.blog__consult-phone>span {
    font-size: 15px;
    color: #000;
    font-weight: 400
}

.blog__consult-phone i {
    font-size: 12px;
    color: #818181;
    font-weight: 400
}

.blog__consult-btn {
    margin-bottom: 0;
}

.blog__banner {
    padding: 20px 16px 5px;
    background: #5854b1;
    background-image: -webkit-gradient(linear, 100% 0, 0 0, from(#0071b6), color-stop(.52, #269cd8), to(#0071b6));
    background-image: -webkit-linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    background-image: -moz-linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    background-image: -o-linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    background-image: linear-gradient(to right, #0071b6 0, #269cd8 52%, #0071b6 100%);
    border: 2px solid #fff;
    color: #fff;
    a {
        color: #fff
    }
    h5 {
        border-bottom: 1px solid #fff;
        font-size: 24px;
        margin-bottom: 15px;
        padding-bottom: 5px;
    }
}

.blog__banner-item {
    margin-bottom: 10px
}

.blog__banner-right {
    margin-top: 4px;
    float: right;
}

.blog__banner-title {
    a {
        font-size: 16px;
        font-weight: 500;
    }
}