.main__map {
    padding-top: 75px;
    position: relative;

    @include media-breakpoint-down(sm) {
        padding-top: 40px;
    }
}
.main__map-before {
    top: -160px;
    left: auto;
    right: -380px;
}

.main__map-title {

    h4 {
        margin: 0 0 10px 0;
        font-size: 36px;
        line-height: 1.3;
        color: #ffffff;
        text-transform: uppercase;

        @include media-breakpoint-down(sm) {
            font-size: 24px;
            line-height: 30px;
            margin-bottom: 0;
        }
    }

    p {
        font-size: 20px;
        line-height: 1.2;
        font-weight: 600;
        margin: 0 0 40px 0;
        color: #ffffff;

        @include media-breakpoint-down(sm) {
            font-size: 14px;
            margin-bottom: 15px;
            line-height: 20px;
        }
    }
}
.main__map-item {
	height: 420px;
    @include media-breakpoint-up(md) {
        height: 520px;
    }

    &--bord-radius{
        border-radius: .25em;
    }
}
.our-promo__item {
    background-repeat: no-repeat;
    background-size: cover;
    height: 400px;
    text-align: center;
    overflow: hidden;
    position: relative;
}

.our-promo__item-link {
    position: absolute;
    bottom: 0;
    display: block;
    height: 100%;
    width: 100%;
    background: url('../images/photo-slider__slide-bg.png') repeat-x bottom;
}

.our-promo__item-logo {
    display: block;
    width: 100%;
    position: absolute;
    bottom: 10px;
    text-align: center;

    img {
        width: auto !important;
        display: inline-block !important;
    }
}

.main-service__item {
    -moz-box-shadow: 0 0 20px rgba(0,0,0,.05);
    box-shadow: 0 0 20px rgba(0,0,0,.05);
    margin-bottom: 30px;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 300px;
    border-radius: 5px;
    transition: all .35s ease;
    @include media-breakpoint-down(sm) {
        height: 200px;
        margin: 0 10px 20px;
    }

    &:before {
        display: block;
        content: '';
        height: 100%;
        width: 100%;
        position: absolute;
        background: rgba(35, 42, 73, .65);
    }

    &:hover,
    &:focus{
        transform: translate(0, -10px);
        .main-service__item-hide {
            visibility: visible;
            opacity: 1;
        }
        .main-service__item-title {
            opacity: 0;
        }
    }

}

.main-service__item-hide {
    overflow: hidden;
    display: flex;
    position: absolute;
    left: 10px;
    top: 10px;
    right: 10px;
    bottom: 10px;
    visibility: hidden;
    opacity: 0;
    background: linear-gradient(94.62deg, #3932B4 1.65%, #5A36BD 98.35%);
    backdrop-filter: blur(20px);
    border-radius: 5px;
    align-items: center;
    transition: .3s all;
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    @include media-breakpoint-down(sm) {
        position: relative;
        //height: auto;
        padding: 15px 0;
    }

    ul {
        position: relative;
        z-index: 10;
        margin: 0 0 0 54px;
        @include media-breakpoint-down(sm) {
            margin: 0 0 0 35px;
        }
        li {
            list-style: disc;
            color: white;
            line-height: 1.3;

            &:before {
                display: none;
            }

            a {
                color: white;
                display: inline-block;
                font-size: 28px;
                line-height: 1.1;
                text-decoration: none;
                padding-bottom: 2px;
                &:hover {
                    opacity: .5;
                    text-decoration: none;
                }

                @include media-breakpoint-down(md) {
                    font-size: 18px;
                }
            }
        }
    }

}

.main-service__item-title {
    position: absolute;
    left: 40px;
    bottom: 43px;
    transition: .3s all;
    
    h5 {
        font-family: 'Gotham Pro';
        line-height: 1.3;
        font-size: 36px;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: white;
        margin: 0;

    }
    @include media-breakpoint-down(md) {
        bottom: 20px;
        left: 20px;
        right: 20px;

        h5 {
            font-size: 22px;
        }
    }
}

.our-advantage__title h4 {
    color: #008bd0;
    font-size: 1.6em;
    text-transform: uppercase;
    font-weight: 600;
    margin-top: 3rem;
}

.main-form {
    background: linear-gradient(96.69deg, #3932B4 1.65%, #5A36BD 98.35%);
    padding: 102px 0 92px;
    margin: 0;
    
    @include media-breakpoint-down(sm) {

        padding: 40px 10px 40px;
    }
}

.main-form__text {
    text-transform: uppercase;
    color: white;

    a {
        color: white;

        &:hover {
            text-decoration: none;
            opacity: .5;
        }
    }

    @include media-breakpoint-down(md) {
        margin: 0 auto;
    }

}

.main-form__text-line-1 {
    font-size: 46px;
    line-height: 1;
    font-weight: 600;
    margin-bottom: 15px;
    @include media-breakpoint-down(lg) {
        font-size: 30px;
    }
    @include media-breakpoint-down(md) {
        font-size: 20px;
        margin-bottom: 10px;
        float: left;
        clear: both;
    }
}

.main-form__text-line-2 {
    font-size: 26px;
    line-height: 1;
    font-weight: 300;
    margin-bottom: 30px;
    @include media-breakpoint-down(lg) {
        font-size: 20px;
    }
    @include media-breakpoint-down(md) {
        font-size: 16px;
        line-height: 1.3;
        margin-bottom: 15px;
        float: left;
        clear: both;
    }
}

.main-form__text-line-3 {
    font-size: 32px;
    line-height: 1;
    font-weight: 600;
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    @include media-breakpoint-down(lg) {
        font-size: 26px;
    }
    @include media-breakpoint-down(md) {
        font-size: 16px;
        margin-bottom: 15px;
        float: left;
        clear: both;
    }

    &:before {
        display: inline-block;
        content: '';
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-size: contain;
        height: 32px;
        width: 40px;
    }
}

.main-form__text-line-4 {
    padding-left: 45px;
    font-size: 18px;
    line-height: 1;
    font-weight: 300;
    margin-bottom: 30px;

    @include media-breakpoint-down(md) {
        font-size: 16px;
        margin-bottom: 15px;
        float: left;
        clear: both;
    }
}


.main-form__text-line-5 {
    font-size: 32px;
    line-height: 1;
    font-weight: 600;
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    @include media-breakpoint-down(lg) {
        font-size: 26px;
    }
    @include media-breakpoint-down(md) {
        font-size: 20px;
        margin-bottom: 15px;
        float: left;
        clear: both;
    }
    @include media-breakpoint-down(sm) {
        .icon-arrow-right {
            display: none;
        }
    }
    &:before {
        display: inline-block;
        content: '';
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-size: contain;
        height: 32px;
        width: 40px;
    }


}

.main-form__text-line-6 {
    padding-left: 45px;
    font-size: 18px;
    line-height: 1.4;
    font-weight: 300;
    @include media-breakpoint-down(lg) {
        margin-bottom: 40px;
    }
    @include media-breakpoint-down(md) {
        font-size: 16px;
        line-height: 22px;
        float: left;
        clear: both;
    }
}

.main-video {
    position: relative;
    margin-top: -1px;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 43%;
        left: 0;
        background: #F0F3F9;
    }
    &--half-no {
        &:before {
            display: none;
        }
    }
}

.main-form-wrap{
    margin-bottom: 75px;

    @include media-breakpoint-down(lg) {
        margin-bottom: 152px;
    }
}
.main-list {
    list-style: none;
    &--margin-top-0 {
        margin-top: 0 !important
    }
    li {
        &:before {
            display: inline-block;
            margin-right: 10px;
            content: "";
        }
    }
}