.news-wrap {
	margin-bottom: 80px;

	@include media-breakpoint-up(lg) {
		margin-bottom: 136px;
	}
}

body .news {
	margin-bottom: 25px;
	&__preview {
		height: 150px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: linear-gradient(94.25deg, #3932B4 1.65%, #5A36BD 98.35%);
		border-radius: .5em;
		margin-bottom: 15px;
		padding: 15px;
		img {
			max-width: 100%;
		}
	}
	&__site{
		display: block;
		font-size: 18px;
		color: #9498A4;
		margin-bottom: 12px;
		text-decoration: none;
	}
	&__sub-desc {
		font-size: 14px;
		color: #ffffff;
		margin: 0 0 2px;
		line-height: 1.6;
	}
	&__date {
		font-size: 14px;
		color: #9498A4;
		margin: 0;
	}
}