.services {
    overflow: hidden;
    position: relative;
    padding-bottom: 140px;
    &__before-bottom {
    	top: initial;
    	left: initial;
        bottom: -196px;
        right: -386px;
    }
}
.service__item {
    margin: 20px 0;
    position: relative;
    transition: .3s all;
    -moz-box-shadow: 0 0 20px rgba(0,0,0,.05);
    box-shadow: 0 0 20px rgba(0,0,0,.05);
    border-radius: .3em;
    overflow: hidden;
    &:hover {

        .service__item-hover {
            height: calc(100% - 20px);
            opacity: 1;
        }

        .service__item-info {
            opacity: 0;
        }

    }
}

.service__item-image {
    height: 100%;
    background-position: center top;
    background-size: cover;
}

.service__item-card {
    color: #fff;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 340px;
    position: relative;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    border-radius: .3em;
    &:before {
        z-index: 1;
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #232A49;
        opacity: 0.65;

    }
}

.service__item-info {
    font-family: "Gotham Pro";
    text-transform: uppercase;
    position: absolute;
    bottom: 0;
    display: block;
    color: #fff;
    width: 100%;
    padding: 40px 0 30px 0;
    transition: all .3s ease 0s;
    z-index: 2;
    font-size: 26px;
    line-height: 34px;
    text-align: center;
    font-weight: 600;
}

.service__item-hover {
    width: calc(100% - 20px);
    height: 0;
    background: linear-gradient(92.56deg, #3932B4 1.65%, #5A36BD 98.35%);
    border-radius: .3em;
    transition: all .3s;
    position: absolute;
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
    opacity: 0;
    z-index: 10;
    padding: 27px 35px;

}

.service__item-text {
    font-size: 18px;
    color: #ffffff;
    font-weight: 300;
    line-height: 1.333;
    
    @include media-breakpoint-down(sm) {
        font-size: 16px;
    }
}

.service__item-button {
    position: absolute;
    bottom: 30px;
    right: 30px;
    z-index: 10;
}

.service__item-caption {
    padding: 0 20px;
}