.footer {
    padding: 40px 0;
    background: #232A49;
    @include media-breakpoint-up(xl) {
        padding: 80px 0 70px;
    }

    h3 {
        font-size: 24px;
        line-height: 1.2;
        color: #ffffff;
        font-weight: 300;
        margin-bottom: 20px;
    }
    ul {
        list-style: none;
        line-height: 1.4;
        margin: 0;

        a {
            color: #9498A4;
            font-size: 18px;
        }

    }
    p {
        font-size: 18px;
        line-height: 1.5;
        color: #ffffff;
        margin: 0;
    }


    &__item{
        padding-left: 20px;
        padding-right: 20px;
        flex: 1 0 100%;
        @include media-breakpoint-up(lg) {
            flex: 1;
        }
        &:first-child{
            @include media-breakpoint-up(xl) {
                padding-left: 60px;
                flex: 0 0 28.5%;
                max-width: 28.5%;

            }
        }
        &:nth-child(2) {
            @include media-breakpoint-up(xl) {
                flex: 0 0 22.5%;
                max-width: 22.5%;
            }
        }
        &:nth-child(3) {
            @include media-breakpoint-up(xl) {
                flex: 0 0 21.5%;
                max-width: 21.5%;
            }
        }
        &:last-child{
            @include media-breakpoint-up(xl) {
               flex: 1;

            }
        }
    }
}

.footer__logo {
    img {
        width: 190px;
        max-width: 100%;
        margin-bottom: 9px;
        @include media-breakpoint-down(lg) {
            margin-bottom: 30px;
        }
    }
}

.footer__line {
    background: linear-gradient(129.9deg, #3932B4 1.65%, #5A36BD 98.35%);
}

.footer__line-content {
    padding: 18px 0;
    @include media-breakpoint-up(lg) {
        padding-left: 40px;
    }
}

.footer__copyright {
    color: #fff;
    font-size: 18px;

    @include media-breakpoint-down(sm) {
        font-size: 16px;
        text-align: center;
    }
}

.footer__social {
    @include media-breakpoint-down(md) {
        margin-bottom: 10px;
    }
    @include media-breakpoint-up(xl) {
        padding-right: 27px;
    }

    a {
        color: #fff;
        font-size: 18px;
        border: 1px solid #fff;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: space-around;
        height: 40px;
        width: 40px;
        margin: 0 0 0 10px;

        &:hover {
            text-decoration: none;
            opacity: .5;
        }
    }
}

.footer__description-address {
    @include media-breakpoint-only(xl) {
        white-space: nowrap;
    }
}