.breadcrumbs {
    font-size: 18px;
    line-height: 1.5;
    margin-top: 50px;
    color: #fff;
    a {
        display: inline-block;
        color: #fff;
        text-decoration: underline;
        font-weight: 600;
        &:hover {
            text-decoration: none;
        }
    }

    span {
        font-weight: 600;
        text-decoration: underline;
    }
}

.pagetitle {
    margin: 17px 0 62px 0;

    @include media-breakpoint-down(sm) {
        margin: 30px 0;
    }

    h1 {
        font-family: "Gotham Pro";
        color: #fff;
        text-transform: uppercase;
        font-weight: 800;
        padding: 0;
        font-size: 24px;
        line-height: 1.5;
        &:before {
            display: inline-block;
            height: 2px;
            width: 30px;
            background: #fff;
            content: '';
            vertical-align: baseline;
            margin-right: 5px;
        }

        @include media-breakpoint-up(xl) {
            font-size: 36px;
            &:before {
                width: 100px;
            }
        }

    }

}