.offices-map {
	padding: 65px 0;
	@include media-breakpoint-up(lg) {
		padding: 124px 0;
	}

	.offices-map__map {
		max-width: 100%;

		height: 250px;
		@include media-breakpoint-up(md) {
			height: 400px;

		}

		@include media-breakpoint-up(lg) {
			height: 500px;

		}
		@include media-breakpoint-up(xl) {
			height: 700px;

		}
	}
}