// CART

.widget__cart * {
    box-sizing: border-box;
}

.widget__cart {
    width: 60px;
    height: 50px;
    position: fixed;
    right: -10px;
    top: 50%;
    margin-top: -150px;
    border-radius: 5px;
    background-color: #269cd8;
    border: 1px solid #2791c7;
    box-sizing: border-box;
    cursor: pointer;
    z-index: 100;
}

.widget__cart:before {
    width: 28px;
    height: 38px;
    position: absolute;
    top: 5px;
    left: 10px;
    background: transparent url('/assets/template/images/promo/icon__cart.png') left top;
    content: "";
}

.cart__link {
    display: block;
    width: 100%;
    height: 50px;
    position: relative;
}

.cart__count {
    position: absolute;
    font-size: 12px;
    top: 5px;
    left: 25px;
    display: inline-block;
    height: 20px;
    width: 20px;
    text-align: center;
    background-color: #ffc61a;
    border: 1px solid #fff;
    border-radius: 50%;
    color: #271f5e;
    line-height: 18px;
    font-weight: bold;
    transform: translate(0);
    transition: all 0.3s ease 0s;
}

.widget__cart:hover .cart__count {
    transform: translate(-11px, -15px);
    transition: all 0.3s ease 0s;
}

.cart-popup {
    min-width: 560px;
    position: relative;
    box-sizing: border-box;
}

.cart-popup * {
    box-sizing: border-box;
}

.cart-popup__body {
    min-height: 305px;
    background: linear-gradient(114.1deg, #239ED1 18.24%, #1F68B3 74.43%);
    padding: 30px 20px;
    color: #fff;
}

.cart-popup__bottom {
    padding: 30px 20px;
    text-align: center;
}

.cart-popup__bottom .btn_rd_default span {
    text-transform: uppercase;
}

.cart-popup__linehor:before {
    position: absolute;
    height: 2px;
    background-color: #ededed;
    top: 5px;
    left: 5px;
    right: 5px;
    content: "";
}

.cart-popup__linehor:after {
    position: absolute;
    height: 2px;
    background-color: #ededed;
    bottom: 5px;
    left: 5px;
    right: 5px;
    content: "";
}

.cart-popup__linever:before {
    position: absolute;
    width: 2px;
    background-color: #ededed;
    top: 7px;
    left: 5px;
    bottom: 7px;
    content: "";
}

.cart-popup__linever:after {
    position: absolute;
    width: 2px;
    background-color: #ededed;
    top: 7px;
    right: 5px;
    bottom: 7px;
    content: "";
}

.popup-card__image {
    height: 160px;
    background-position: top center;
    background-size: cover;
    margin-bottom: 6px;
}

.popup-card__list {
    text-align: center;
    width: 600px;
    padding: 0 20px;
}

.popup-card {
    display: inline-block;
    margin: 0 10px;
    width: 160px;
    vertical-align: top;
    position: relative;

}

.cart-popup__header {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 22px;
}

.popup-card__caption {

    font-size: 13px;
    line-height: 120%;
    height: 48px;
    overflow: hidden;
}

.popup-card__remove {
    position: absolute;
    top: 3px;
    right: 3px;
    width: 24px;
    height: 24px;
    background: transparent url('/assets/template/images/promo/popup-card__close.jpg') left top no-repeat;
    border: 0 none;
}

.popup-card__remove:hover {
    background: transparent url('/assets/template/images/promo/popup-card__close.jpg') right top no-repeat;
}

.popup-card__arrow {
    border: 0 none;
    color: transparent;
    display: block;
    height: 24px;
    margin-bottom: 20px;
    position: absolute;
    width: 14px;
    z-index: 100;
}

.popup-card__arrow--prev {
    background: transparent url("/assets/template/images/promo/popup-card__arrows.png") left top no-repeat;
    left: 0px;
    top: 75px;
}

.popup-card__arrow--next {
    background: transparent url("/assets/template/images/promo/popup-card__arrows.png") right top no-repeat;
    right: 0px;
    top: 75px;
}

@media (max-width: 993px) {
    .popup-card__list {
        width: 220px;
        margin-left: auto;
        margin-right: auto;
    }
    .cart-popup {
        min-width: auto;
    }
    .popup-card {
    }
    .cart-popup__header {
        font-size: 18px;
        line-height: 110%;
    }
    .cart-popup__body {
        padding-bottom: 0;
    }
}

// ORDER

.p-order {
    margin-bottom: 30px;
}

.order__count {
    color: #2f3587;
    font-size: 18px;
    margin-bottom: 22px;
}

.order__count span {
    font-weight: bold;
}

.order__list * {
    box-sizing: border-box;
}

.order__list {
    margin-left: -15px;
    margin-right: -15px;
}

.order__list::after {
    clear: both;
    content: "";
    display: table;
}

.order__item {
    margin-bottom: 30px;
    text-align: center;
}

.order__card {
    height: 290px;
    margin: 0 auto;
    max-width: 290px;
    overflow: hidden;
    position: relative;
}

.order__image {
    background-position: 0 center;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
}

.order__info {
    background: transparent url("/assets/template/images/promo/caption__gradient.png") repeat-x scroll left top;
    bottom: 0;
    color: #fff;
    display: block;
    height: 100%;
    left: 0;
    opacity: 1;
    overflow: hidden;
    padding: 45px 18px 12px;
    position: absolute;
    right: 0;
    transform: translateY(204px);
    transition: all 0.3s ease 0s;
}

.order__caption {
    font-weight: bold;
    font-size: 16px;
    line-height: 110%;
    margin-bottom: 10px;
    min-height: 32px;
    padding-bottom: 3px;
    text-align: center;
}

.order-form * {
    box-sizing: border-box;
}

.order-form textarea,
.order-form input[type="text"] {
    background-color: #fff;
    border: 1px solid #2a9ed9;
    color: #92acb6;
    font-size: 20px;
    height: 54px;
    line-height: 54px;
    margin-bottom: 16px;
    padding: 0 14px;
    width: 100%;
    max-width: 405px;
}

.order-form textarea {
    height: 194px;
}

.order-form__left {
    width: 50%;
    float: left;
    padding-right: 20px;
}

.order-form__right {
    width: 50%;
    float: left;
    padding-left: 20px;
}

.order-form__border {
    border-left: 2px solid #2a9ed9;
    border-right: 2px solid #2a9ed9;
    border-bottom: 2px solid #2a9ed9;
    padding: 2px;
    position: relative;
    clear: both;
}

.order-form__border:before {
    position: absolute;
    left: -2px;
    top: -2px;
    height: 2px;
    background-color: #2a9ed9;
    width: 189px;
    content: "";
}

.order-form__border:after {
    position: absolute;
    right: -2px;
    top: -2px;
    height: 2px;
    background-color: #2a9ed9;
    width: 189px;
    content: "";
}

.order-form {
    border-left: 1px solid #2a9ed9;
    border-right: 1px solid #2a9ed9;
    border-bottom: 1px solid #2a9ed9;
    padding: 20px 25px;
    position: relative;
}

.order-form:before {
    position: absolute;
    left: -1px;
    top: -1px;
    height: 1px;
    background-color: #2a9ed9;
    width: 185px;
    content: "";
}

.order-form:after {
    position: absolute;
    right: -1px;
    top: -1px;
    height: 1px;
    background-color: #2a9ed9;
    width: 185px;
    content: "";
}

.order-form__header {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 22px;
    color: #2f3587;
    margin-top: -31px;
    clear: both;
    margin-bottom: 14px;
}

.order-form__lead {
    text-align: center;
    color: #2f3587;
    font-size: 14px;
}

.order-form #msOrder {
    margin-top: 22px;
}

.order-form__submit {
    text-align: center;
}

.order-form__submit .btn_rd_default {
    text-transform: uppercase;
    width: 290px;;
}

.order-form__submit .btn_rd_default span {
    padding: 14px 10px !important;
}

.order-form__personal,
.order-form__personal a {
    font-size: 13px;
    text-align: center;
    margin-bottom: 14px;
}

.order-form__personal a {
    font-size: 13px;
    text-align: center;
}

@media (max-width: 993px) {
    .order-form__border,
    .order-form {
        border: 0 none;
        padding: 0;
    }
    .order-form__border:before,
    .order-form__border:after,
    .order-form:before,
    .order-form:after {
        display: none;
    }
    .order-form__header {
        font-size: 18px;
        line-height: 120%;
    }
    .order-form__left,
    .order-form__right {
        float: none;
        width: 100%;
        padding: 0;
    }
}

.form-promo {
    padding: 50px;
}

.form-promo__title {
    margin: 30px 0;
    position: relative;
    top: 0;
}

.form-promo__wrapper {
    border-top: 3px solid #fff;
    &:before {
        display: none;
    }
}

// jGrowl

#jGrowl .ms2-message-success:before {
    width: 28px;
    height: 38px;
    position: absolute;
    top: 23px;
    left: 24px;
    background: transparent url('/assets/template/images/promo/icon__cart.png') left top;
    content: "";
}

#jGrowl .ms2-message-success a {
    color: #fff;
}

#jGrowl .ms2-message-success a:hover {
    text-decoration: none;
}

#jGrowl .ms2-message-error {
    min-height: 68px;
}

#jGrowl .ms2-message-error:before {
    width: 28px;
    height: 38px;
    position: absolute;
    left: 24px;
    font-size: 24px;
    content: "\f071";
    color: #fff;
    font-family: FontAwesome;
}

#jGrowl.jGrowl {
    z-index: 99999;
}

#jGrowl.jGrowl.top-right {
    top: auto;
    right: auto;
    left: 10px !important;
    bottom: 10px !important;
}

#jGrowl .ms2-message-error .jGrowl-close,
#jGrowl .ms2-message-success .jGrowl-close {
    position: absolute;
    right: 6px;
    top: 1px;
    font-size: 24px;
}

#jGrowl .ms2-message-error,
#jGrowl .ms2-message-success {
    background-color: rgba(47, 53, 135, 0.9) !important;
    padding-left: 78px;
    font-size: 18px;

    position: relative;
    min-width: 420px;
    min-height: 90px;
    box-sizing: border-box;
    line-height: 150%;
    padding-top: 19px;
}

@media (max-width: 993px) {
    #jGrowl .ms2-message-error,
    #jGrowl .ms2-message-success {
        min-width: 100%;
        font-size: 16px;
    }
}

// TABLE

.table__price table {
    border: 1px #269cd8 solid;
}

.table__price td {
    text-align: left;
    padding: 3px;
    table-layout: fixed;
    width: 1%;
}

.table__price td:first-child,
.table__price th:first-child {
    text-align: left;
    padding-left: 15px;
}

.table__price th {
    text-align: left;
    padding: 3px;
    table-layout: fixed;
    width: 1%;
}

.table__price tr:first-child {
    background: #269cd8 none repeat scroll 0 0;
    font-size: 20px;
    font-weight: bold;
    height: 20px;
}

.table__price tr:nth-child(even) {
    background: #eaf4f9 none repeat scroll 0 0;
    font-size: 18px;
    height: 50px;
}

.table__price tr:nth-child(2n+3) {
    background: white none repeat scroll 0 0;
    font-size: 18px;
    height: 50px;
}

.table__price tr:first-child td, tr:first-child th {
    color: #fff;
    font-size: 18px;
    font-weight: normal;
    text-align: left;

    height: 43px;
    line-height: 125%;
}

