}

.product__description {
    padding: 50px 40px;
}

.product__content {
    font-size: 18px;
    line-height: 24px;
}

.property__item {
    margin-bottom: 10px;
    font-size: .9em;
}

.property__value {
    font-weight: bold;
}

.product__prices-key {
    text-transform: uppercase;
